.header {
  padding: 30px 14.7% 0 14.7%;
  min-height: 1070px;
  background: url(../imgs/header-bg.png);
  background-color: rgba(39, 42, 50, 0.8);
  background-size: cover;
  background-repeat: no-repeat;
}

.header__top {
  max-width: 1200px;
  margin: 0 auto;
  height: 75px;
}

.header__top .logo {
  width: 55px;
  height: 75px;
}

.header__top-nav {
  background: transparent;
  right: -15px;
  margin-top: 10px;
  border: none;
}

.header .container-fluid, .header .navbar-collapse {
  padding: 0;
}

.header .nav__menu-item {
  list-style-type: none;
  display: inline-block;
  margin-left: 30px;
}

@media all and (max-width: 931px) {
  .header .nav__menu-item {
    margin-left: 10px;
    transition: all 0.5s;
  }
}

.header .nav__menu-item-link {
  color: #fffefe;
  font-family: "Proxima Nova";
  font-size: 24px;
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  position: relative;
  border-bottom: 2px solid transparent;
}

@media all and (max-width: 931px) {
  .header .nav__menu-item-link {
    font-size: 22px;
    transition: all 0.7s;
  }
}

.header .nav__menu-item-link:hover {
  border-bottom: 2px solid #fff;
  transform: scale(1.07);
  transition: all 0.7s;
}

.header__title {
  max-width: 1100px;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
  margin-top: 275px;
}

.header__title h1 {
  color: #fefefb;
  font-family: "Proxima Nova";
  font-size: 71px;
  font-weight: 700;
  text-transform: uppercase;
}

.header__title p {
  color: #fefdfd;
  font-family: "PT Sans";
  font-size: 30px;
  font-weight: 300;
  font-style: italic;
  max-width: 670px;
  margin: 0 auto;
}

.navbar-header {
  max-width: 300px;
}

.navbar-toggle {
  width: 41px;
  border: none;
  right: -20px;
  border-radius: 4px;
}

.dropdown-toggle:hover {
  background-color: transparent;
}

@media all and (max-width: 767px) {
  .nav__menu-item-link {
    font-size: 16px;
  }
  .container-fluid {
    width: 300px;
    padding-right: 20px;
  }
  .navbar {
    width: 300px;
    float: right;
  }
  .navbar-collapse {
    border: none;
  }
}
